/* Start::object-fit */
.object-fit-container {
  @apply flex items-center justify-center;
  img,
  video {
    @apply w-[15.625rem] h-[15.625rem];
  }
}
.object-fit-contain {
  @apply object-contain #{!important};
}
.object-fit-cover {
  @apply object-cover #{!important};
}
.object-fit-fill {
  @apply object-cover #{!important};
}
.object-fit-scale {
  @apply object-scale-down #{!important};
}
.object-fit-none {
  @apply object-none #{!important};
}
.object-fit-sm-contain {
  @apply sm:object-contain #{!important};
}
.object-fit-md-contain {
  @apply md:object-contain #{!important};
}
.object-fit-lg-contain {
  @apply lg:object-contain #{!important};
}
.object-fit-xl-contain {
  @apply xl:object-contain #{!important};
}
.object-fit-xxl-contain {
  @apply xxl:object-contain #{!important};
}

/* End::object-fit */